<script>
  import foot from "./public/foot.vue";
  import top from "./public/top.vue";
  import topMember from "./public/topMember.vue";
  import {hex_md5} from "../utils/md5";
  import back from "../mixins/back";
  import timeFormater from "@/utils/timeFormate";
  import moment from "moment";
  export default {
    components: {
      foot,
      top,
      topMember
    },
    mixins: [back],
    filters: {
      timeFormater
    },
    data() {
      return {
        priceForm: "",
        mytotal: 0,
        member: "",
        password: "",
        oldPassword: "",
        tableData: [],
        page: 1,
        size: 10,
        total: 0,
        editInforDiv: false,
        editMmDiv: false,
        memberInfor: {},
        welcomeWord: ['', '尊敬的VIP', '尊敬的超级VIP'],
        vipLevel: ['普通会员', 'VIP会员', '超级VIP会员'],
        systemNotice:[],
        normalNotice:[],
      };
    },
    mounted() {
      if (sessionStorage.getItem("token") == null) {
        this.$router.push({
          path: "/login"
        });
      }
      this.getPrice();
      this.taskList();
      this.getMoney();
      this.getMember();
      this.getNotice(1);
      this.getNotice(2);
    },
    methods: {
      moment,
      toArticle(item){
        this.$router.push({path:'/article',query:{id:item.id,type:item.type}});
      },
      applyDialog(){
        this.$alert("您好，底单申请请联系客服<br/>QQ:"+this.memberInfor.qq+"<br/>微信:"+this.memberInfor.wx, '底单申请', {
          confirmButtonText: '确定',dangerouslyUseHTMLString: true
        });
      },
      getInfor(data) {
        this.member = data;
      },
      getMember() {
        let that = this;
        this.axios
          .get("/giftmall/webSite/info", {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              domain: this.url,

              Authorization: sessionStorage.getItem("token")
            }
          })
          .then(function (response) {
            if (response.data.code == 200) {
              that.memberInfor = response.data.data;
              sessionStorage.setItem(
                "memberInfor",
                JSON.stringify(that.memberInfor)
              );
            }
          });
      },
      editInfor() {
        let that = this;
        // this.axios.put(process.env.API_HOST+'user',
        this.axios
          .put(
            "/giftmall/user",
            this.qs.stringify({qq: this.member.qq, wx: this.member.wx}),
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // 'domain':'taobao1.tianyalei.com',
                domain: this.url,
                Authorization: sessionStorage.getItem("token")
              }
            }
          )
          .then(function (response) {
            if (response.data.code == 200) {
              that.$message({
                message: "修改成功",
                type: "success"
              });
            } else {
              that.$message({
                message: response.data.message,
                type: "warning"
              });
            }
          });
      },
      editPassword() {
        let that = this;
        // this.axios.put(process.env.API_HOST+'user/password',
        this.axios
          .put(
            "/giftmall/user/password",
            this.qs.stringify({
              oldPassword: hex_md5(this.oldPassword),
              password: hex_md5(this.password)
            }),
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // 'domain':'taobao1.tianyalei.com',
                domain: this.url,
                Authorization: sessionStorage.getItem("token")
              }
            }
          )
          .then(function (response) {
            if (response.data.code == 200) {
              that.$message({
                message: "修改成功",
                type: "success"
              });
              that.$router.push({
                path: "/login"
              });
            } else {
              that.$message({
                message: response.data.message,
                type: "warning"
              });
            }
          });
      },
      getMoney() {
        let that = this;
        this.axios
          .get(
            // process.env.API_HOST+"wallet/myTotal",
            "/giftmall/wallet/myTotal",
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                domain: this.url,

                Authorization: sessionStorage.getItem("token")
              }
            }
          )
          .then(function (response) {
            if (response.data.code == 200) {
              that.mytotal = response.data.data;
            }
          });
      },
      getNotice(type) {
        let that = this;
        this.axios
          .get(
            // process.env.API_HOST+"wallet/myTotal",
            "/giftmall/notice?size=5&type="+type,
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                domain: this.url,

                Authorization: sessionStorage.getItem("token")
              },
            }
          )
          .then(response => {
            if (response.data.code == 200) {
              if(type === 1){
                this.systemNotice = response.data.data.list
              }else{
                this.normalNotice = response.data.data.list
              }
            }
          });
      },
      taskList() {
        let that = this;
        this.axios
          .get(
            // process.env.API_HOST+"task?&page="+(that.page-1)+'&size='+that.size,
            "/giftmall/task?&page=" + (that.page - 1) + "&size=" + that.size,
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                domain: this.url,
                Authorization: sessionStorage.getItem("token")
              }
            }
          )
          .then(function (response) {
            if (response.data.code == 200) {
              for (var i = 0; i < response.data.data.list.length; i++) {
                if (response.data.data.list[i].payTime) {
                  response.data.data.list[i].payTime = that.gettime(
                    response.data.data.list[i].payTime
                  );
                }
              }
              that.tableData = response.data.data.list;
              that.total = response.data.data.totalCount;
            }
          });
      },
      getPrice() {
        let that = this;
        this.axios
          .get(
            // process.env.API_HOST+"expressPrice/my",
            "/giftmall/expressPrice/my",
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                domain: this.url,
                Authorization: sessionStorage.getItem("token")
              }
            }
          )
          .then(function (response) {
            if (response.data.code == 200) {
              that.priceForm = response.data.data;
              sessionStorage.setItem(
                "priceForm",
                JSON.stringify(response.data.data)
              );
            }
          });
      },
      capitalDetail() {
        this.$router.push({
          path: "/capitalDetail"
        });
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.size = val;
        this.taskList();
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.page = val;
        this.taskList();
      },
      gettime(t) {
        var _time = new Date(t);
        var year = _time.getFullYear(); //2017
        var month =
          _time.getMonth() > 8
            ? _time.getMonth() + 1
            : "0" + (_time.getMonth() + 1); //7
        var date = _time.getDate() > 9 ? _time.getDate() : "0" + _time.getDate(); //10
        var hour =
          _time.getHours() > 9 ? _time.getHours() : "0" + _time.getHours(); //10
        var minute =
          _time.getMinutes() > 9 ? _time.getMinutes() : "0" + _time.getMinutes(); //56
        var second =
          _time.getSeconds() > 9 ? _time.getSeconds() : "0" + _time.getSeconds(); //15

        return (
          year +
          "-" +
          month +
          "-" +
          date +
          "   " +
          hour +
          ":" +
          minute +
          ":" +
          second
        ); //这里自己按自己需要的格式拼接
      }
    }
  };
</script>

